.hobbies{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 100%;
    min-height: 100vh;
    text-align: center;
    padding-bottom: 30px;

    /* background-image: url('../../assets/dslr_icon.svg'); */
    background: rgba(255, 255, 255, 0.6);
    background-image: url('../../assets/dslr_icon.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.hobbies h1{
    color: black;
    margin: 0 0 70px 0;
}

