.hobbies .hobbiesContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;

    width: 90%;
}

.hobbies .hobbiesContainer #photographyShowcase{
    max-width: 800px;

    background: white;
    border-radius: 10px;

    margin: 10px;
}

.hobbies .hobbiesContainer #otherInterests{
    width: 700px;
    height: 300px;

    background: #FAFAFA;
    border-radius: 10px;

    margin: 10px;
}

.hobbies .hobbiesContainer #otherInterests h1{
    color: black;
    margin: 15px 0 15px 0;
}